<template>
  <div>
    <div class="input-wrap">
      <input
        type="text"
        class="todo-input"
        placeholder="What needs to be done"
        v-model="newTodo"
        @keyup.enter="createTodo"
      />
      <button @click="createTodo" class="active">Enter</button>
    </div>
    <p class="error" v-if="error">{{ error }}</p>
    <transition-group
      name="fade"
      enter-active-class="animated fadeInLeft"
      leave-active-class="animated fadeOutRight"
    >
      <div class="todo-item" v-for="(todo) in todosFiltered" :key="todo._id">
        <div class="todo-item-left">
          <label class="checkbox">
            <input type="checkbox" v-model="todo.completed" />
          </label>
          <div
            v-if="!todo.editing"
            @dblclick="editTodo(todo)"
            class="todo-item-label"
            :class="{ completed : todo.completed }"
          >{{ todo.title }}</div>
          <input
            v-else
            class="todo-item-edit"
            type="text"
            v-model="todo.title"
            @blur="doneEdit(todo)"
            @keyup.enter="doneEdit(todo)"
            @keyup.esc="cancelEdit(todo)"
            v-focus
          />
        </div>
        <h6
          class="date"
        >{{`${todo.createdAt.getDate()}/${todo.createdAt.getMonth()}/${todo.createdAt.getFullYear()}`}}</h6>
        <div class="remove-item" @click="deleteTodo(todo._id)">&times;</div>
      </div>
    </transition-group>

    <div class="extra-container">
      <div>
        <label class="checkbox">
          <input class="checkbox" type="checkbox" :checked="!anyRemaining" @change="checkAllTodos" /> Check All
        </label>
      </div>
      <div>{{ remaining }} items left</div>
    </div>

    <div class="extra-container">
      <div>
        <button :class="{ active: filter == 'all' }" @click="filter = 'all'">All</button>
        <button :class="{ active: filter == 'active' }" @click="filter = 'active'">Active</button>
        <button :class="{ active: filter == 'completed' }" @click="filter = 'completed'">Completed</button>
      </div>

      <div>
        <transition name="fade">
          <button v-if="showClearCompletedButton" @click="clearCompleted">Clear Completed</button>
        </transition>
      </div>
    </div>
    <footer class="footer-copyright">
      <h6>&copy; Matteo Fenu 2020</h6>
    </footer>
  </div>
</template>

<script>
import TodosService from "../TodosService";
export default {
  name: "todo-list",
  data() {
    return {
      newTodo: "",
      beforeEditCache: "",
      filter: "all",
      error: "",
      todos: []
    };
  },
  computed: {
    remaining() {
      return this.todos.filter(todo => !todo.completed).length;
    },
    anyRemaining() {
      return this.remaining != 0;
    },
    todosFiltered() {
      if (this.filter == "all") {
        return this.todos;
      } else if (this.filter == "active") {
        return this.todos.filter(todo => !todo.completed);
      } else if (this.filter == "completed") {
        return this.todos.filter(todo => todo.completed);
      }
      return this.todos;
    },
    showClearCompletedButton() {
      return this.todos.filter(todo => todo.completed).length > 0;
    }
  },
  directives: {
    focus: {
      inserted: function(el) {
        el.focus();
      }
    }
  },
  async created() {
    try {
      this.todos = await TodosService.getTodos();
    } catch (err) {
      this.error = err.message;
    }
  },
  methods: {
    async createTodo() {
      if (this.newTodo.trim().length == 0) {
        return;
      }
      await TodosService.createTodo(this.newTodo);
      this.todos = await TodosService.getTodos();
      this.newTodo = "";
    },
    editTodo(todo) {
      this.beforeEditCache = todo.title;
      todo.editing = true;
    },
    async doneEdit(todo) {
      if (todo.title.trim() == "") {
        todo.title = this.beforeEditCache;
      }
      await TodosService.updateTodo(todo._id, todo.title);
      this.todos = await TodosService.getTodos();
      todo.editing = false;
    },
    cancelEdit(todo) {
      todo.title = this.beforeEditCache;
      todo.editing = false;
    },
    async deleteTodo(id) {
      await TodosService.deleteTodo(id);
      this.todos = await TodosService.getTodos();
    },
    checkAllTodos() {
      this.todos.forEach(todo => (todo.completed = event.target.checked));
    },
    async clearCompleted() {
      const completedTodos = this.todos.filter(todo => todo.completed === true);
      completedTodos.forEach(
        async todo => await TodosService.deleteTodo(todo._id)
      );
      this.todos = await TodosService.getTodos();
    }
  }
};
</script>

<style>
@import url("https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.5.2/animate.min.css");
.todo-input {
  width: 100%;
  padding: 10px 18px;
  font-size: 18px;
  border: 4px solid #8e98f0;
  border-radius: 8px;
}

.checkbox {
  padding-top: 6px;
}
.checkbox > span {
  color: #34495e;
  padding: 0.5rem 0.25rem;
}

.checkbox > input {
  height: 20px;
  width: 20px;
  -webkit-appearance: none;
  -moz-appearance: none;
  -o-appearance: none;
  appearance: none;
  border: 2px solid #8e98f0;
  border-radius: 4px;
  outline: none;
  transition-duration: 0.3s;
  background-color: none;
  cursor: pointer;
}

.checkbox > input:checked {
  border: 4px solid #8e98f0;
  background-color: #34495e;
}

.checkbox > input:active {
  border: 2px solid #34495e;
}
.todo-input:focus {
  outline: 0;
}
.todo-item {
  padding: 10px;
  border: 4px solid #8e98f0;
  box-shadow: 0 0 0 4px hsl(256, 77%, 88%), 0 0 0 8px hsl(242, 63%, 82%);
  border-radius: 8px;
  margin-bottom: 20px;
  display: grid;
  align-items: center;
  grid-template-columns: 10fr 1fr 1fr;
  animation-duration: 0.3s;
}
.error {
  color: #f33;
}
.date {
  float: right;
  color: gray;
  font-size: 0.5em;
  padding-right: 8px;
  margin: 0;
}
.remove-item {
  justify-self: right;
  cursor: pointer;
  margin-right: 5px;
  font-size: 1.5em;
  color: #7c78ee;
}
.remove-item:hover {
  color: #435466;
}
.todo-item-left {
  display: flex;
  align-items: center;
}
.todo-item-label {
  font-size: 20px;

  border: 1px solid white;
  margin-left: 8px;
}
.todo-item-edit {
  font-size: 20px;
  color: #2c3e50;
  margin-left: 12px;
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  font-family: "Avenir", Helvetica, Arial, sans-serif;
}
.todo-item-edit:focus {
  outline: none;
}
.completed {
  text-decoration: line-through;
  color: grey;
}
.extra-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 16px;
  border-top: 1px solid lightgrey;
  padding-top: 14px;
  margin-bottom: 14px;
}
.footer-copyright {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top: 1px solid lightgrey;
}
.footer-copyright > h6 {
  font-weight: 200;
  margin: 5px;
  color: lightgray;
}
button {
  font-size: 14px;
  background-color: none;
  background: none;
  appearance: none;
  border: 4px solid #8e98f0;
  border-radius: 8px;
  min-height: 25px;
  font-weight: 700;
  margin: 0 4px;
  cursor: pointer;
}
.input-wrap {
  display: flex;
  margin-bottom: 16px;
}
.active {
  background: #8e98f0;
  color: white;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>